import React from "react";
import {
  BrowserRouter as Router,
  Route,
  Redirect,
  Switch,
} from "react-router-dom";
import styled from "styled-components";
import Main from "Routes/Main";
import Signup from "Routes/Signup";
import Login from "Routes/Login";
import Service from "Routes/Service";
import Navbar from "./Navbar";

const MainContainer = styled.div`
  margin-left: 5em;
  height: 100%;
`;

const Routers = () => {
  return (
    <Router>
      <Navbar />
      <MainContainer>
        <Switch>
          <Route path="/" exact component={Main} navbar={Navbar} />
          <Route path="/Signup" component={Signup} navbar={Navbar} />
          <Route path="/Login" component={Login} navbar={Navbar} />
          <Route path="/Service" component={Service} navbar={Navbar} />
          <Redirect from="*" to="/" />
        </Switch>
      </MainContainer>
    </Router>
  );
};

export default Routers;
